body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #000;
}

.landing-container {
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.glasses-container {
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.glasses-container svg {
    width: 80%;
    height: 80%;
}

.outline {
    stroke: #3949ab;
    stroke-width: 4;
    fill: none;
}

.curtain {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    animation: dropCurtain 3s ease-in-out forwards;
}

@keyframes dropCurtain {
    0% {
        transform: translateY(0%);
    }
    100% {
        transform: translateY(100%);
    }
}